.WikiMirrorNotice {
	background: #fff;
	border: 0.1rem solid #0645ad;
	cursor: default;
	direction: ltr;
	display: flex;
	justify-content: center;
	text-align: center;
	-webkit-user-select: none;
	        user-select: none;
}

.skin-minerva .WikiMirrorNotice {
	background: transparent;
	border: 0.1rem solid #fff;
	color: inherit;
}

.WikiMirrorNotice > *:first-child {
	border-right: 0.1rem dotted #0645ad;
	font-size: 0.8em;
	font-weight: 500;
	line-height: 1.8;
	margin: 0;
	min-width: calc(100% - 3rem);
	padding: 0.4em !important;
	width: -webkit-fit-content;
	width: -moz-fit-content;
	width: fit-content;
	word-break: break-word;
}

.skin-minerva .WikiMirrorNotice > *:first-child {
	border-right: 0.1em dotted #fff;
}

.WikiMirrorNotice button {
	background: #fff;
	border: 0;
	color: inherit;
	cursor: pointer;
	font-family: inherit;
	font-size: 0.8em;
	padding: 0.4em 0;
	width: 3rem;
}

.WikiMirrorNotice button:active,
.WikiMirrorNotice button:focus,
.WikiMirrorNotice button:hover {
	background: #f7f7f7;
	color: #0645ad;
}

.skin-minerva .WikiMirrorNotice button {
	background: transparent;
	color: inherit;
}

.skin-minerva .WikiMirrorNotice button:active,
.skin-minerva .WikiMirrorNotice button:focus,
.skin-minerva .WikiMirrorNotice button:hover {
	background: rgba(255, 255, 255, 0.2);
}

.skin-minerva .WikiMirrorTip {
	border: 0.1rem solid #0645ad;
}

.WikiMirrorTip > *:first-child {
	font-size: 0.7em;
	border-right: 0;
}

.skin-minerva div.mw-highlight {
	background-color: #f8f8f8;
}

.skin-minerva dl dd {
	overflow: unset;
}

.skin-minerva ul.mw-contributions-list .comment,
.skin-minerva ul.special .comment {
	word-break: break-word;
}

.skin-minerva .mw-highlight pre {
	position: unset;
}

.skin-minerva .mw-notification-area {
	z-index: 5;
}

.skin-minerva #footer-places-cookiestatement::after {
	display: none;
}

.skin-timeless .content-table-left,
.skin-timeless .content-table-right {
	height: auto !important;
}

.skin-timeless .infobox caption,
.skin-timeless .wikitable > caption {
	width: auto !important;
}

.skin-timeless .catlinks-allhidden {
	display: none !important;
}

.skin-timeless #catlinks-sidebar {
	display: none;
}

.skin-timeless #catlinks {
	background: #f8f9fa;
	border: solid #eaecf0;
	border-width: 1px 1px 0.2em;
	box-shadow: 0 1px 2px 1px rgba(0, 0, 0, 0.02);
	display: inherit;
	font-size: 0.95em;
	margin: 1em 0 0;
	padding: 1em 2em;
}

.skin-timeless #catlinks div {
	margin: 0 0 0.35em;
}

.skin-timeless #catlinks div:last-of-type {
	margin: 0;
}

.skin-timeless #catlinks li {
	border-left: 0;
}

.mw-parser-output a.external,
sup.reference {
	-webkit-user-select: none;
	        user-select: none;
}

.mw-parser-output a.external.free,
.mw-parser-output a.external.text {
	-webkit-user-select: unset;
	        user-select: unset;
}

.oo-ui-windowManager-modal > .oo-ui-dialog {
	-webkit-backdrop-filter: blur(5px);
	        backdrop-filter: blur(5px);
}

